import { $authHost } from './index';

export async function addBasketDevice(basketId, deviceId, name, fullName, price) {
    const response = await $authHost.post('/api/basket', { basketId, deviceId, name, fullName, price });
    return response.data;
}

export async function removeBasketDevice(basketId, deviceId) {
    const response = await $authHost.delete('/api/basket', { data: { basketId, deviceId } });
    return response.data;
}

export async function changeBasketDevice(basketId, deviceId, amount) {
    const response = await $authHost.patch(`/api/basket/${basketId}/${deviceId}`, { amount });
    return response.data;
}

export async function fetchingBasketDevices(id) {
    const response = await $authHost.get('/api/basket/' + id);
    return response.data;
}

export async function fetchingOneBasketDevice(basketId, deviceId) {
    const response = await $authHost.get(`/api/basket/${basketId}/${deviceId}`);
    return response.data;
}