import React, { useContext, useEffect, useState } from 'react';
import { v4 } from 'uuid';
import { useNavigate, useParams } from 'react-router-dom';
import { averageCalculation, getFormatPrice } from '../utils/utils';
import SectionTemplate from '../components/SectionTemplate';
import Button from '../components/button/Button';
import styles from './styles/Product.module.scss';
import SvgGlobal from '../components/SvgGlobal';
import { fetchingOneDevice, updateDevice } from '../http/deviceAPI';
import { Context } from '../index';
import Loader from '../components/loader/Loader';
import { observer } from 'mobx-react-lite';
import { PAGE_404_ROUTE } from '../router/paths';
import { addBasketDevice, fetchingOneBasketDevice, removeBasketDevice } from '../http/basketAPI';
import Alert from '../components/alert/Alert';
import Modal from '../components/modal/Modal';
import RatingStars from '../components/stars/RatingStars';
import { fetchingAllRatingsById } from '../http/ratingAPI';

const Product = observer(() => {
    const { store } = useContext(Context);
    const { id } = useParams();
    const navigate = useNavigate();

    const [ visible, setVisible ] = useState(false);
    const [ device, setDevice ] = useState(null);
    const [ basketDevice, setBasketDevice ] = useState(null);

    const basketId = store.userStore.user.id || null;

    useEffect(() => {
        store.deviceStore.setLoading(true);
        store.deviceStore.setAverageRating(0);

        fetchingOneDevice(id)
            .then(data => setDevice(data))
            .catch(() => navigate(PAGE_404_ROUTE))
            .finally(() => store.deviceStore.setLoading(false));

        fetchingAllRatingsById(id)
            .then(ratings => {
                const ratingsSum = ratings.map(i => i.rate).reduce((sum, i) => sum + i, 0);
                ratingsSum !== 0
                    ? store.deviceStore.setAverageRating(averageCalculation(ratingsSum, ratings.length))
                    : store.deviceStore.setAverageRating(0);
            })
            .catch(e => console.log(e.message));

        if (store.userStore.isAuth) {
            fetchingOneBasketDevice(basketId, id)
                .then(data => setBasketDevice(data))
                .catch(e => console.log(e));
        }
    }, []);

    useEffect(() => {
        updateDevice(id, { rating: store.deviceStore.averageRating })
            .then(data => setDevice(data))
            .catch(e => console.log(e.message));
    }, [ store.deviceStore.averageRating ]);

    const addToBasket = () => {
        if (!store.userStore.isAuth) {
            store.alertStore.toggle(true, 'warning', 'Войдите чтобы добавить товар в корзину.');
            return;
        }

        addBasketDevice(basketId, device.id, device.name, device.fullName, device.price)
            .then(data => setBasketDevice(data))
            .catch(e => console.log(e.message));
    };
    const removeFromBasket = () => {
        removeBasketDevice(basketId, device.id)
            .then(data => {
                store.basketStore.removeBasket(data);
                setBasketDevice(null);
            })
            .catch(e => console.log(e.message));
    };
    const clickRatingHandler = () => setVisible(true);

    return (
        <SectionTemplate name="product" styles={styles.product}>
            {device && !store.deviceStore.loading
                ?
                <div className={styles.product__wrapper}>
                    <Modal state={visible} setState={setVisible}>
                        <h2>Оцените товар</h2>
                        <RatingStars ratingForDeviceId={id}/>
                    </Modal>
                    <Alert/>
                    <div className={styles.product__top}>
                        <div className={styles.product__image}>
                            <img src={`${process.env.REACT_APP_API_URL}/${device.img}`} alt={device.name}/>
                        </div>
                        <div className={styles.product__info}>
                            <h1>{device.fullName}</h1>
                            <div className={styles.product__info_row}>
                                <p>{getFormatPrice(device.price, 'ru-RU', ' руб.')}</p>
                                <span onClick={clickRatingHandler}>
                                    {device.rating}
                                    <SvgGlobal
                                        name="rating-star"
                                        fill="#DDE775"
                                        w={50}
                                        h={50}
                                    />
                                </span>
                            </div>
                            <Button type="button" onClick={basketDevice ? removeFromBasket : addToBasket}>
                                {basketDevice ? 'Удалить из корзины' : 'В корзину'}
                                <SvgGlobal
                                    name="cart"
                                    fill="white"
                                    w={18}
                                    h={17}
                                />
                            </Button>
                        </div>
                    </div>
                    <div className={styles.product__bottom}>
                        {device.info[0].description &&
                            <div className={styles.product__desription}>
                                <h2>Описание:</h2>
                                {device.info[0].description.split('\n').map(d =>
                                    <p key={v4()}>{d}</p>
                                )}
                            </div>
                        }
                        {device.info[0].characteristics &&
                            <div className={styles.product__characts}>
                                <h2>Характеристики:</h2>
                                <ul className={styles.product__characts_list}>
                                    {JSON.parse(device.info[0].characteristics).map(c =>
                                        <li key={c.num}>
                                            <h3>{c.title}:&nbsp;</h3>
                                            <p>{c.value}</p>
                                        </li>
                                    )}
                                </ul>
                            </div>
                        }
                    </div>
                    <Alert/>
                </div>
                :
                <Loader/>
            }
        </SectionTemplate>
    );
});

export default Product;
