import React, { useContext, useEffect } from 'react';
import styles from './RatingStars.module.scss';
import Star from './Star';
import { Context } from '../../index';
import { fetchingUserRatingById } from '../../http/ratingAPI';
import { observer } from 'mobx-react-lite';

const RatingStars = observer(({ ratingForDeviceId }) => {
    const { store } = useContext(Context);

    const ratingStars = [
        {value: 1, ratingForDeviceId},
        {value: 2, ratingForDeviceId},
        {value: 3, ratingForDeviceId},
        {value: 4, ratingForDeviceId},
        {value: 5, ratingForDeviceId}
    ]

    useEffect(() => {
        fetchingUserRatingById(store.userStore.user.id, ratingForDeviceId)
            .then(userRating => store.userStore.setUserRate(userRating.rate))
            .catch(() => store.userStore.setUserRate(0));
    }, []);

    return (
        <div className={styles.rating__stars}>
            {ratingStars.map(r => <Star key={r.value} {...r}/>)}
        </div>
    );
});

export default RatingStars;
