import React, { useState } from 'react';
import styles from './Header.module.scss';
import { Link } from 'react-router-dom';
import { HOME_ROUTE } from '../../router/paths';
import Navigation from '../navigation/Navigation';
import Auth from '../auth/Auth';
import MenuButton from '../MenuButton/MenuButton';

const Header = () => {
    const [menuIsOpen, setMenuIsOpen] = useState(false);

    return (
        <header className={styles.header}>
            <div className={'container'}>
                <div className={styles.header__wrapper}>
                    <Link className={styles.logo} to={HOME_ROUTE}>Online<span>Store</span></Link>
                    <Navigation state={menuIsOpen} click={setMenuIsOpen}/>
                    <Auth state={menuIsOpen}/>
                    <MenuButton state={menuIsOpen} click={setMenuIsOpen}/>
                </div>
            </div>
        </header>
    );
};

export default Header;