import React, { useContext } from 'react';
import { Link } from 'react-router-dom';
import { Context } from '../../index';
import styles from './Auth.module.scss';
import { LOGIN_ROUTE } from '../../router/paths';
import { observer } from 'mobx-react-lite';

const Auth = observer(({ state }) => {
    const { store } = useContext(Context);

    const logOutAndLogIn = e => {
        if (!store.userStore.isAuth) return;

        localStorage.removeItem('token');
        store.userStore.setIsAuth(false);
        store.userStore.setUser({id: 'Незнакомец'});
    }

    return (
        <div className={state ? [styles.auth, styles._isOpen].join(' ') : styles.auth}>
            {<Link to={LOGIN_ROUTE} onClick={logOutAndLogIn}>{store.userStore.isAuth ? 'Выйти' : 'Войти'}</Link>}
        </div>
    );
});

export default Auth;