import { Navigate } from 'react-router-dom';
import * as PATH from './paths';
import Home from '../pages/Home';
import Catalog from '../pages/Catalog';
import Product from '../pages/Product';
import Registration from '../pages/Registration';
import Login from '../pages/Login';
import Basket from '../pages/Basket';
import Page404 from '../pages/Page404';
import Admin from '../pages/Admin';

export const authRoutes = [
    { path: PATH.ADMIN_ROUTE, element: <Admin/> },
    { path: PATH.BASKET_ROUTE, element: <Basket/> }
];

export const publicRoutes = [
    { path: PATH.HOME_ROUTE, element: <Home/>, index: true },
    { path: PATH.CATALOG_ROUTE, element: <Catalog/> },
    { path: PATH.PRODUCT_ROUTE, element: <Product/> },
    { path: PATH.REGISTRATION_ROUTE, element: <Registration/> },
    { path: PATH.LOGIN_ROUTE, element: <Login/> },
    { path: PATH.PAGE_404_ROUTE, element: <Page404/> },
    { path: PATH.INVALID_ROUTE, element: <Navigate to={PATH.PAGE_404_ROUTE} replace/> }
];