export const getFormatPrice = (price, format = 'ru-RU', symbol = '') => {
    let s
    switch (format) {
        case 'ru-RU':
            s = symbol || ' ₽';
            break;
        case 'de-DE':
            s = symbol || ' €';
            break;
        case 'ja-JP': 
            s = symbol || ' ￥';
            break;
        case 'en-EN ':
            s = symbol || ' $';
            break;
        default:
            return s = symbol;
    }
    return new Intl.NumberFormat(format).format(price) + s;
};

export const regexpCheck = (regexp, value) => regexp.test(value);
export const averageCalculation = (sum, length) => Math.round(sum / length);