import React, { useContext } from 'react';
import { authRoutes, publicRoutes } from '../router/routes';
import { Route, Routes } from 'react-router-dom';
import { Context } from '../index';
import { observer } from 'mobx-react-lite';
import Loader from './loader/Loader';
import { ROLES } from '../types/types';
import { ONLY_ADMIN_ROUTE } from '../router/paths';

const AppRouter = observer(() => {
    const { store } = useContext(Context);

    if (store.userStore.isLoading)
        return <main className="main"><Loader/></main>;

    return (
        <main className="main">
            <Routes>
                {store.userStore.isAuth
                    ?
                    store.userStore.user.role === ROLES.ADMIN
                        ? [ ...authRoutes, ...publicRoutes ]
                            .map(route => <Route key={route.path} {...route} />)
                        : [ ...authRoutes, ...publicRoutes ]
                            .filter(route => route.path !== ONLY_ADMIN_ROUTE)
                            .map(route => <Route key={route.path} {...route} />)
                    :
                    publicRoutes.map(route => <Route key={route.path} {...route} />)
                }
            </Routes>
        </main>
    );
});

export default AppRouter;
