import React from 'react';
import styles from './Input.module.scss';

const Input = ({type, name, placeholder, label, value, onChange, required, isValid, error}) => {
    const rootClasses = isValid || isValid === undefined
        ? styles.inputBlock__item
        : [styles.inputBlock__item, styles.inputBlock__item_error].join(' ');

    return (
        <div className={styles.inputBlock}>
            <input
                type={type}
                name={name}
                placeholder={placeholder}
                value={value}
                onChange={onChange}
                className={rootClasses}
                required={required}
            />
            {label && <span>{label}</span>}
            {error && <span className={styles.inputBlock__error}>{error}</span>}
        </div>
    );
};

export default Input;