import { $authHost, $host } from './index';
import jwtDecode from 'jwt-decode';

export async function registration(email, password) {
    const response = await $host.post('/api/user/registration', { email, password, role: 'USER' });
    localStorage.setItem('token', response.data.token);
    return jwtDecode(response.data.token);
}

export async function login(email, password) {
    const response = await $host.post('/api/user/login', { email, password });
    localStorage.setItem('token', response.data.token);
    return jwtDecode(response.data.token);
}

export async function check() {
    const response = await $authHost.get('/api/user/auth');
    localStorage.setItem('token', response.data.token);
    return jwtDecode(response.data.token);
}

export async function change(email, role, aEmail, aPassword) {
    const response = await $authHost.patch('/api/user/update', { email, role, aEmail, aPassword });
    localStorage.setItem('token', response.data.token);
    return jwtDecode(response.data.token);
}