import React, { useContext, useEffect, useState } from 'react';
import SvgGlobal from '../SvgGlobal';
import styles from './RatingStars.module.scss';
import { createRating, fetchingAllRatingsById } from '../../http/ratingAPI';
import { averageCalculation } from '../../utils/utils';
import { Context } from '../../index';
import { observer } from 'mobx-react-lite';

const Star = observer(({ value, ratingForDeviceId }) => {
    const { store } = useContext(Context);
    const [ fill, setFill ] = useState(false);

    useEffect(() => {
        Number(store.userStore.userRate) >= value ? setFill(true) : setFill(false);
    }, [ store.userStore.userRate ]);

    const updateAverageLocalRating = () => {
        fetchingAllRatingsById(ratingForDeviceId)
            .then(ratings => {
                const ratingsSum = ratings.map(i => i.rate).reduce((sum, i) => sum + i);
                ratingsSum !== 0
                    ? store.deviceStore.setAverageRating(averageCalculation(ratingsSum, ratings.length))
                    : store.deviceStore.setAverageRating(0);
            });
    };
    const clickHandler = rate => {
        if (store.userStore.isAuth) {
            createRating(store.userStore.user.id, ratingForDeviceId, rate)
                .then(data => {
                    store.alertStore.toggle(true, 'success', 'Спасибо за оценку!');
                    store.userStore.setUserRate(data.rate);
                    updateAverageLocalRating();
                })
                .catch((e) => console.log(e.message));
        } else {
            store.alertStore.toggle(true, 'warning', 'Необходимо авторизоваться!');
        }
    };

    return (
        <div className={styles.rating__star} onClick={() => clickHandler(value)}>
            <SvgGlobal
                name="rating-star"
                fill={fill ? '#DDE775' : 'transparent'}
                stroke="#DDE775"
                w={50}
                h={50}
            />
        </div>
    );
});

export default Star;
