import React, { useContext, useEffect, useMemo, useState } from 'react';
import styles from './Select.module.scss';
import { observer } from 'mobx-react-lite';
import { Context } from '../../index';

const Select = observer(({ options, name = 'Выберете пункт', onChange, prefix, allExist = false }) => {
    const [ isOptionsOpen, setIsOptionsOpen ] = React.useState(false);
    const [ selectedOption, setSelectedOption ] = React.useState(null); // __ '' or null __ => Все
    const [ selectedOptionName, setSelectedOptionName ] = React.useState('');
    const [ newOptions, setNewOptions ] = useState([]);

    const getNameById = id => {
        newOptions.filter(option => {
            if (option.id === Number(id) || option.id === id) setSelectedOptionName(option.name);
        });
    };

    useEffect(() => {
        if (allExist) {
            setNewOptions([ { id: '', name: `Все ${prefix}` }, ...options ]); // __ '' or null __ => Все
            setSelectedOption('');
        } else {
            setNewOptions(options);
            setSelectedOption(1);
        }
    }, [ options ]);

    useEffect(() => {
        onChange(selectedOption);
        getNameById(selectedOption);
    }, [ newOptions ]);

    useEffect(() => {
        onChange(selectedOption);
        getNameById(selectedOption);
    }, [ selectedOption ]);

    const toggleOptions = () => {
        setIsOptionsOpen(!isOptionsOpen);
    };

    const setSelectedThenCloseDropdown = (id) => {
        setSelectedOption(id);
        setIsOptionsOpen(false);
    };

    return (
        <div className={styles.select}>
            <div className="select__container">
                <button type="button" onClick={toggleOptions}>
                    {newOptions.length ? selectedOptionName || name : 'Загрузка...'}
                </button>
                <ul className={`${styles.options} ${isOptionsOpen ? styles.show : ''}`}>
                    <li>{name}</li>
                    {newOptions.length && newOptions.map((option, index) => (
                        <li
                            key={option.id}
                            tabIndex={index}
                            onClick={() => {
                                setSelectedThenCloseDropdown(option.id);
                            }}
                        >
                            {option.name}
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    );
});

export default Select;