import React, { useContext, useEffect, useRef, useState } from 'react';
import styles from './Filters.module.scss';
import { Context } from '../../index';
import FilterItem from './FilterItem';
import { observer } from 'mobx-react-lite';
import { fetchingTypes } from '../../http/typeAPI';

const Filters = observer(() => {
    const { store } = useContext(Context);
    const [isOpen, setIsOpen] = useState(false);
    const [currentType, setCurrentType] = useState(''); // __ '' or null __ => Все
    const typeAll = {id: '', name: 'Все категории'};

    useEffect(() => {
        fetchingTypes().then(data => {
            store.typesStore.setTypes(data);
            store.deviceStore.setType(currentType);
        }).catch(e => {
            console.error(e.message);
        });
    }, []);

    const toggleFilter = () => setIsOpen(!isOpen);
    const changeType = id => {
        store.deviceStore.setType(id);
        setCurrentType(id);
        setIsOpen(false);
    } // __ '' or null __ => Все


    return (
        <div className={isOpen ? [styles.filters, styles.filters_isOpen].join(' ') : styles.filters}>
            <button
                type='button'
                onClick={toggleFilter}
                className={isOpen ? [styles.filters__close, styles.filters__close_isOpen].join(' ') : styles.filters__close}>
                <div>
                    <span></span>
                    <span></span>
                    <span></span>
                </div>
            </button>
            <FilterItem
                type='button'
                name={typeAll.name}
                clickHandler={() => changeType('')}
                active={typeAll.id === currentType}
            />
            {store.typesStore.types.map(type =>
                <FilterItem
                    key={type.id}
                    type='button'
                    name={type.name}
                    clickHandler={() => changeType(type.id)}
                    active={type.id === currentType}
                />
            )}
        </div>
    );
});

export default Filters;