import { makeAutoObservable } from 'mobx';

export default class DeviceStore {
    constructor() {
        this._loading = true;
        this._devices = [];
        this._page = 1;
        this._limit = 8;
        this._totalCount = 0;
        this._brand = null;
        this._type = null;
        this._averageRating = 0;
        makeAutoObservable(this);
    }

    setLoading(bool) {
        this._loading = bool;
    }

    setDevices(devices) {
        this._devices = devices;
    }

    setBrand(brand) {
        this.setPage(1);
        this._brand = brand;
    }

    setType(type) {
        this.setPage(1);
        this._type = type;
    }

    setPage(page) {
        this._page = page;
    }

    setLimit(limit) {
        this._limit = limit;
    }

    setAverageRating(average) {
        this._averageRating = average;
        console.log(this._averageRating);
    }

    setTotalCount(count) {
        this._totalCount = count;
    }

    get loading() {
        return this._loading;
    }

    get devices() {
        return this._devices;
    }

    get brand() {
        return this._brand;
    }

    get type() {
        return this._type;
    }

    get page() {
        return this._page;
    }

    get limit() {
        return this._limit;
    }

    get averageRating() {
        return this._averageRating;
    }

    get totalCount() {
        return this._totalCount;
    }
}