import React from 'react';
import styles from './Modal.module.scss';

const Modal = ({ children, state, setState }) => {
    const rootStyles = state ? [styles.modal, styles.show].join(' ') : styles.modal;

    return (
        <div className={rootStyles} onClick={e => setState(false)}>
            <div className={styles.modal__container} onClick={e => e.stopPropagation()}>
                <div className={styles.modal__wrapper}>
                    {children}
                </div>
            </div>
        </div>
    );
};

export default Modal;