import { makeAutoObservable } from 'mobx';

export default class UserStore {
    constructor() {
        this._isAuth = false;
        this._isLoading = true;
        this._user = {};
        this._userRate = 0;
        makeAutoObservable(this);
    }

    setIsAuth(bool) {
        this._isAuth = bool;
        this.setIsLoading(false);
    }

    setIsLoading(bool) {
        this._isLoading = bool;
    }

    setUserRate(rate) {
        this._userRate = rate;
    }

    setUser(user) {
        this._user = user;
    }

    get isAuth() {
        return this._isAuth;
    }

    get isLoading() {
        return this._isLoading;
    }

    get userRate() {
        return this._userRate;
    }

    get user() {
        return this._user;
    }
}