import React, { useContext } from 'react';
import SectionTemplate from '../components/SectionTemplate';
import { Link } from 'react-router-dom';
import styles from './styles/Home.module.scss';
import { CATALOG_ROUTE } from '../router/paths';
import SvgGlobal from '../components/SvgGlobal';
import { Context } from '../index';
import { observer } from 'mobx-react-lite';

const Home = observer(() => {
    const { store } = useContext(Context);

    return (
        <SectionTemplate name={'home'} styles={styles.home}>
            <div className={styles.home__wrapper}>
                <div className={styles.homeBanner}>
                    <h1>Привет {store.userStore.isAuth ? store.userStore.user.email : 'Незнакомец'}!<br/>Начни путешествие по
                        Online<span>Store</span></h1>
                    <Link to={CATALOG_ROUTE} className={styles.homeBanner__start}>
                        <span>Начать</span>
                        <button type={'button'}>
                            <SvgGlobal name="b-arrow" fill="white"/>
                        </button>
                    </Link>
                </div>
            </div>
        </SectionTemplate>
    );
});

export default Home;