import React, { useContext, useRef } from 'react';
import styles from './Counter.module.scss';
import CounterButton from './CounterButton';
import { Context } from '../../index';
import { removeBasketDevice } from '../../http/basketAPI';

const Counter = (props) => {
    const { store } = useContext(Context);
    const basketId = store.userStore.user.id || null;

    const plus = useRef();
    const minus = useRef();

    const removeBasketItem = id => {
        removeBasketDevice(basketId, id)
            .then(data => store.basketStore.removeBasket(data))
            .catch(e => console.log(e.message));
    }

    const increment = e => {
        if (props.count >= 100) {
            e.target.disabled = true;
            return;
        }
        minus.current.disabled = false;
        props.setCount(prev => prev += 1);
    };

    const decrement = e => {
        props.setCount(prev => prev -= 1);
        if (props.count <= 1) {
            e.target.disabled = true;
            removeBasketItem(props.id);
            return;
        }
        plus.current.disabled = false;
    };

    return (
        <div className={styles.counter}>
            <CounterButton ref={minus} onClick={decrement} type="button">-</CounterButton>
            <span>{props.count}</span>
            <CounterButton ref={plus} onClick={increment} type="button">+</CounterButton>
        </div>
    );
};

export default Counter;
