import React from 'react';
import styles from './ProductsCard.module.scss';
import { getFormatPrice } from '../../utils/utils';

const ProductCard = ({ img, name, fullName, rating, price, clickHandler }) => {
    return (
        <div className={styles.products__card} onClick={clickHandler}>
            <img src={`${process.env.REACT_APP_API_URL}/${img}`} alt="image"/>
            <div className={styles.products__card_banner}>
                <div>
                    <p className={styles.title}>{fullName}</p>
                    <span className={styles.rating}>
                        {rating}
                        <svg width="15" height="14" viewBox="0 0 15 14" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path
                                d="M7.5 0L9.18386 5.18237H14.6329L10.2245 8.38525L11.9084 13.5676L7.5 10.3647L3.09161 13.5676L4.77547 8.38525L0.367076 5.18237H5.81614L7.5 0Z"
                                fill="#DDE775"/>
                        </svg>
                    </span>
                </div>
                <div>
                    <h3>{name}</h3>
                    <div className={styles.price}>{getFormatPrice(price, 'ru-RU', ' руб.')}</div>
                </div>
            </div>
        </div>
    );
};

export default ProductCard;