import React from 'react';
import styles from './FiltersItem.module.scss';

const FilterItem = ({ type, name, clickHandler, active }) => {
    return (
        <button
            className={active ? [styles._isActive, styles.filters__item].join(' ') : styles.filters__item}
            type={type}
            onClick={clickHandler}
        >
            {name}
        </button>
    );
};

export default FilterItem;