import React, { useContext } from 'react';
import styles from '../../pages/styles/Basket.module.scss';
import { getFormatPrice } from '../../utils/utils';
import { Context } from '../../index';
import { observer } from 'mobx-react-lite';

const BasketTotal = observer(() => {
    const { store } = useContext(Context);
    return (
        <div className={styles.basket__total}>
            <p>Итого: <span>{getFormatPrice(store.basketStore.totalPrice)}</span></p>
        </div>
    );
});

export default BasketTotal;
