import { makeAutoObservable, runInAction } from 'mobx';

export default class AlertStore {
    constructor() {
        this._initialState = {
            visible: false,
            type: '',
            text: 'Новое уведомление!'
        }
        this._alert = this._initialState;
        makeAutoObservable(this);
    }

    toggle(visible, type, text, ms = 2000) {
        this.close();
        this._alert = { visible, type, text };
        setTimeout(() => {
            runInAction(() => this.close());
        }, [ ms ]);
    }

    open(type, text) {
        this.close();
        this._alert = { visible: true, type, text }
    }

    close() {
        this._alert = this._initialState
    }

    get state() {
        return this._alert;
    }
}