import React, { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import styles from './Navigation.module.scss';
import { Context } from '../../index';
import { observer } from 'mobx-react-lite';
import { navLinks } from '../../router/nav';
import { ROLES } from '../../types/types';

const Navigation = observer(({state, click}) => {
    const { pathname } = useLocation();
    const { store } = React.useContext(Context);
    const [ links, setLinks ] = useState([]);

    const changeLinks = (arr, endpoint, key) => {
        return arr.map(item => item.to === endpoint
            ? { ...item, [key]: true }
            : { ...item, [key]: false });
    };

    useEffect(() => {
        if (store.userStore.isAuth) {
            store.userStore.user.role === ROLES.ADMIN
                ? setLinks(changeLinks(navLinks, pathname, 'isActive'))
                : setLinks(changeLinks(navLinks, pathname, 'isActive').filter(link => !link.onlyAdmin));
        } else {
            setLinks(changeLinks(navLinks, pathname, 'isActive').filter(link => !link.onlyAuth));
        }
        click(false);
    }, [ store.userStore.isLoading, pathname ]);

    return (
        <nav className={state ? [styles.nav, styles._isOpen].join(' ') : styles.nav}>
            <ul className={styles.nav__list}>
                {links && links.map(link =>
                    <li
                        key={link.to}
                        onClick={() => setLinks(changeLinks(links, link.to, 'isActive'))}
                        className={link.isActive
                            ? [ styles.nav__list_item, styles.isActive ].join(' ')
                            : styles.nav__list_item
                        }
                    >
                        <Link to={link.to}>{link.name}</Link>
                    </li>)
                }
            </ul>
        </nav>
    );
});

export default Navigation;