import React from 'react';
import { Link } from 'react-router-dom';
import { LOGIN_ROUTE, REGISTRATION_ROUTE } from '../../router/paths';
import Input from '../input/Input';
import Button from '../button/Button';
import styles from './Form.module.scss';
import useFormAuth from '../../hooks/useFormAuth';

const Form = () => {
    const {isLogin, error, state, changeHandler, authAndRegistrationHandler} = useFormAuth();

    return (
        <form className={styles.form} onSubmit={authAndRegistrationHandler}>
            <h2>{isLogin ? 'Вход' : 'Регистрация'}</h2>
            {error && <span className={styles.error}>{error}</span>}
            <Input
                type="text"
                name="email"
                placeholder="Почта"
                value={state.email}
                onChange={changeHandler}
                isValid={state.emailIsValid}
                error={state.errors.emailErr}
                required
            />
            <Input
                type="password"
                name="password"
                placeholder="Пароль"
                value={state.password}
                onChange={changeHandler}
                isValid={state.passwordIsValid}
                error={state.errors.passwordErr}
                required
            />
            <div className={styles.form__footer}>
                <p>{isLogin ? 'Нет аккаунта?' : 'Есть аккаунт?'}&nbsp;
                    <Link to={isLogin ? REGISTRATION_ROUTE : LOGIN_ROUTE}>
                        {isLogin ? 'Регистрация' : 'Войти'}
                    </Link>
                </p>
                <Button type="submit" disabled={!(state.emailIsValid && state.passwordIsValid)}>
                    {isLogin ? 'Войти' : 'Зарегистрироваться'}
                </Button>
            </div>
        </form>
    );
};

export default Form;