import { makeAutoObservable } from 'mobx';

export default class BasketStore {
    constructor() {
        this._isLoading = true;
        this._basketDevices = [];
        this._totalPrice = 0;
        makeAutoObservable(this);
    }

    setIsLoading(bool) {
        this._isLoading = bool;
    }

    setBasket(basketDevices) {
        this._basketDevices = basketDevices;
        this.setTotalPrice();
    }

    setAmount(id, amount) {
        this.setBasket(this.basket.map(device => device.deviceId === id ? {...device, amount} : device));
        this.setTotalPrice();
    }

    setTotalPrice() {
        this._totalPrice = this.basket.reduce((sum, el) => {
            return sum + (el.price * el.amount)
        }, 0)
    }

    removeBasket(id) {
        this.setBasket(this.basket.filter(i => i.deviceId !== Number(id)));
    }

    get isLoading() {
        return this._isLoading;
    }

    get basket() {
        return this._basketDevices;
    }

    get totalPrice() {
        return this._totalPrice;
    }
}