import React, { useContext, useEffect, useState } from 'react';
import { v4 } from 'uuid';
import { Context } from '../index';
import styles from './styles/Admin.module.scss';
import stylesOfInput from '../components/input/Input.module.scss';
import SectionTemplate from '../components/SectionTemplate';
import Modal from '../components/modal/Modal';
import Input from '../components/input/Input';
import Textarea from '../components/textarea/Textarea';
import Button from '../components/button/Button';
import Select from '../components/select/Select';
import { createType, fetchingTypes } from '../http/typeAPI';
import { createBrand, fetchingBrands } from '../http/brandAPI';
import { observer } from 'mobx-react-lite';
import { createDevice } from '../http/deviceAPI';
import { ROLES } from '../types/types';
import { change } from '../http/userAPI';
import Alert from '../components/alert/Alert';
import useValidation from '../hooks/useValidation';

const types = {
    ADMIN_TYPE: 'type',
    ADMIN_BRAND: 'brand',
    ADMIN_DEVICE: 'device',
    ADMIN_USER: 'user'
};

const Admin = observer(() => {
    const { store } = useContext(Context);

    useEffect(() => {
        fetchingTypes().then(data => store.typesStore.setTypes(data));
        fetchingBrands().then(data => store.brandStore.setBrands(data));
    }, []);

    const [ visible, setVisible ] = React.useState(false);
    const [ selected, setSelected ] = React.useState('');

    const [ typeValue, setTypeValue ] = useState('');
    const [ brandValue, setBrandValue ] = useState('');

    const [ deviceNameValue, setDeviceNameValue ] = useState('');
    const [ deviceFullNameValue, setDeviceFullNameValue ] = useState('');
    const [ deviceDescription, setDeviceDescription ] = useState('');
    const [ devicePriceValue, setDevicePriceValue ] = useState('');
    const [ deviceFile, setDeviceFile ] = useState(null);
    const [ deviseCharacteristics, setDeviseCharacteristics ] = React.useState([
        { num: v4(), title: '', value: '' }
    ]);
    const [ info, setInfo ] = useState({
        description: deviceDescription,
        characteristics: JSON.stringify(deviseCharacteristics)
    });

    const [ newUser, setNewUser ] = useState({
        email: '',
        password: '',
        role: ROLES.USER,
        errors: { emailErr: '', passwordErr: '' },
        emailIsValid: true,
        passwordIsValid: true,
    });
    const { emailValidation, passwordValidation } = useValidation(newUser, setNewUser);

    useEffect(() => {
        setInfo({ ...info, description: deviceDescription, characteristics: JSON.stringify(deviseCharacteristics) });
    }, [ deviseCharacteristics, deviceDescription ]);

    // 1. Выбор действия для админа
    const selectHandler = (e, name) => {
        e.preventDefault();
        setSelected(name);
        setVisible(true);
    };

    // 1. Выбор картинки
    // 2. Выбор типа
    // 3. Выбор бренда
    const selectFiles = e => setDeviceFile(e.target.files[0]);
    const changeType = value => store.deviceStore.setType(value);
    const changeBrand = value => store.deviceStore.setBrand(value);
    const changeRole = value => setNewUser({ ...newUser, role: value });

    // Заполнение характеристик
    const changeCharacteristics = (key, value, num) => {
        setDeviseCharacteristics(deviseCharacteristics.map(item => item.num === num ? {
            ...item,
            [key]: value
        } : item));
    };

    // 1. Добавление поля для характеристик
    // 2. Удаление поля для характеристик
    const addInfoField = () => {
        setDeviseCharacteristics([ ...deviseCharacteristics, { num: v4(), title: '', value: '' } ]);
    };
    const removeInfoField = num => {
        setDeviseCharacteristics([ ...deviseCharacteristics ].filter(i => i.num !== num));
    };

    // 1. Добавления типа
    // 2. Добавления бреда
    // 3. Добавления девайса
    const addType = () => {
        createType({ name: typeValue }).then(() => setTypeValue(''));
    };
    const addBrand = () => {
        createBrand({ name: brandValue }).then(() => setBrandValue(''));
    };
    const addDevice = () => {
        const formData = new FormData();
        formData.append('name', deviceNameValue.trim());
        formData.append('fullName', deviceFullNameValue.trim());
        formData.append('price', devicePriceValue.trim());
        formData.append('img', deviceFile);
        formData.append('typeId', store.deviceStore.type);
        formData.append('brandId', store.deviceStore.brand);
        formData.append('info', JSON.stringify(info));
        createDevice(formData)
            .then(() => {
                setDeviceNameValue('');
                setDeviceFullNameValue('');
                setDevicePriceValue('');
                setDeviceDescription('');
                setDeviceFile(null);
                setDeviseCharacteristics([ { num: v4(), title: '', value: '' } ]);
                changeType(1);
                changeBrand(1);
            });
    };
    const changeUserHandler = e => {
        switch (e.target.name) {
            case 'email':
                emailValidation(e.target.value);
                break;
            case 'password':
                passwordValidation(e.target.value);
                break;
        }
    };
    const changeUser = () => {
        const changeSuccess = () => {
            store.alertStore.toggle(
                true,
                'success',
                `Пользователь ${newUser.email} имеет роль ${newUser.role}.`,
                3000
            );
        };
        const changeError = (res) => {
            store.alertStore.toggle(
                true,
                'error',
                `${res.response.data.message}`,
                3000
            );
        };

        if (!newUser.emailIsValid || !newUser.passwordIsValid) {
            return store.alertStore.toggle(
                true,
                'error',
                `Неккоректный email или пароль!`,
                3000
            );
        }

        change(newUser.email, newUser.role, store.userStore.user.email, newUser.password)
            .then(() => changeSuccess())
            .catch(data => changeError(data));
    };

    // 2. Обработка создания
    const createHandler = e => {
        e.preventDefault();
        switch (selected) {
            case types.ADMIN_TYPE:
                addType();
                break;
            case types.ADMIN_BRAND:
                addBrand();
                break;
            case types.ADMIN_DEVICE:
                addDevice();
                break;
            case types.ADMIN_USER:
                changeUser();
                break;
        }
        setSelected('');
        setVisible(false);
    };

    return (
        <SectionTemplate name={'notFound'} styles={styles.admin}>
            <div className={styles.admin__wrapper}>
                <h1>Админ панель</h1>
                <ul className={styles.admin__panel}>
                    <li>
                        <button
                            type="button"
                            name={types.ADMIN_TYPE}
                            onClick={e => selectHandler(e, e.target.name)}
                        >
                            Добавить тип
                        </button>
                    </li>
                    <li>
                        <button
                            type="button"
                            name={types.ADMIN_BRAND}
                            onClick={e => selectHandler(e, e.target.name)}
                        >
                            Добавить бренд
                        </button>
                    </li>
                    <li>
                        <button
                            type="button"
                            name={types.ADMIN_DEVICE}
                            onClick={e => selectHandler(e, e.target.name)}
                        >
                            Добавить девайс
                        </button>
                    </li>
                    <li>
                        <button
                            type="button"
                            name={types.ADMIN_USER}
                            onClick={e => selectHandler(e, e.target.name)}
                        >
                            Изменить пользователя
                        </button>
                    </li>
                </ul>
            </div>
            <Modal state={visible} setState={setVisible}>
                {selected === types.ADMIN_TYPE && <h2>Добавить тип</h2>}
                {selected === types.ADMIN_BRAND && <h2>Добавить бренд</h2>}
                {selected === types.ADMIN_DEVICE && <h2>Добавить девайс</h2>}
                {selected === types.ADMIN_USER && <h2>Изменить пользователя</h2>}
                <form onSubmit={createHandler}>
                    {selected === types.ADMIN_TYPE &&
                        <Input
                            type="text"
                            placeholder="Новый тип"
                            value={typeValue}
                            onChange={e => setTypeValue(e.target.value)}
                            required
                        />
                    }
                    {selected === types.ADMIN_BRAND &&
                        <Input
                            type="text"
                            placeholder="Новый бренд"
                            value={brandValue}
                            onChange={e => setBrandValue(e.target.value)}
                            required
                        />
                    }
                    {selected === types.ADMIN_DEVICE &&
                        <>
                            <Input
                                type="text"
                                placeholder="Название"
                                value={deviceNameValue}
                                onChange={e => setDeviceNameValue(e.target.value)}
                                required
                            />
                            <Input
                                type="text"
                                placeholder="Полное название"
                                value={deviceFullNameValue}
                                onChange={e => setDeviceFullNameValue(e.target.value)}
                                required
                            />
                            <Input
                                type="text"
                                placeholder="Цена"
                                value={devicePriceValue}
                                onChange={e => setDevicePriceValue(e.target.value)}
                                required
                            />
                            <Textarea
                                type="text"
                                placeholder="Описание"
                                value={deviceDescription}
                                onChange={e => setDeviceDescription(e.target.value)}
                                max="1000"
                            />
                            <Select
                                options={store.typesStore.types}
                                name={'Выберете тип'}
                                onChange={changeType}
                                allExist={false}
                            />
                            <Select
                                options={store.brandStore.brands}
                                name={'Выберете бренд'}
                                onChange={changeBrand}
                                allExist={false}
                            />
                            <label htmlFor="file" className={stylesOfInput.label}>
                                {deviceFile ? deviceFile.name : 'Выберете изображение'}
                                <input
                                    id="file"
                                    type="file"
                                    onChange={selectFiles}
                                />
                            </label>
                            <Button
                                className={'addInfo'}
                                type="button"
                                name="addInfo"
                                onClick={addInfoField}
                            >
                                Добавить поле
                            </Button>
                            {deviseCharacteristics.length !== 0 &&
                                <div className={styles.form__info}>
                                    {deviseCharacteristics.map((item) =>
                                        <div key={item.num} className={styles.form__info_item}>
                                            <Input
                                                type="text"
                                                placeholder="Название"
                                                value={item.title}
                                                onChange={e =>
                                                    changeCharacteristics('title', e.target.value, item.num)
                                                }
                                                required
                                            />
                                            <Input
                                                type="text"
                                                placeholder="Значение"
                                                value={item.value}
                                                onChange={e =>
                                                    changeCharacteristics('value', e.target.value, item.num)
                                                }
                                                required
                                            />
                                            <Button
                                                type="button"
                                                onClick={() => removeInfoField(item.num)}
                                            >
                                                Удалить
                                            </Button>
                                        </div>
                                    )}
                                </div>
                            }
                        </>
                    }
                    {selected === types.ADMIN_USER &&
                        <>
                            <Input
                                type="text"
                                name="email"
                                placeholder="Email пользователя"
                                value={newUser.email}
                                onChange={changeUserHandler}
                                isValid={newUser.emailIsValid}
                                error={newUser.errors.emailErr}
                                required
                            />
                            <Input
                                type="password"
                                name="password"
                                placeholder="Подтвердите пароль"
                                value={newUser.password}
                                onChange={changeUserHandler}
                                isValid={newUser.passwordIsValid}
                                error={newUser.errors.passwordErr}
                                required
                            />
                            <label
                                className={newUser.role === ROLES.USER ? [ stylesOfInput.label, stylesOfInput.label_isActive ].join(' ') : stylesOfInput.label}>
                                Пользователь
                                <Input
                                    type="radio"
                                    name="radioRole"
                                    value={ROLES.USER}
                                    onChange={e => changeRole(e.target.value)}
                                    checked={newUser.role === ROLES.USER}
                                />
                            </label>
                            <label
                                className={newUser.role === ROLES.ADMIN ? [ stylesOfInput.label, stylesOfInput.label_isActive ].join(' ') : stylesOfInput.label}>
                                Админ
                                <Input
                                    type="radio"
                                    name="radioRole"
                                    value={ROLES.ADMIN}
                                    onChange={e => changeRole(e.target.value)}
                                    checked={newUser.role === ROLES.ADMIN}
                                />
                            </label>
                        </>
                    }
                    <Button type="submit">Добавить</Button>
                </form>
            </Modal>
            <Alert/>
        </SectionTemplate>
    );
});

export default Admin;