import React, { useContext, useEffect, useState } from 'react';
import SectionTemplate from '../components/SectionTemplate';
import Filters from '../components/filters/Filters';
import Products from '../components/products/Products';
import styles from './styles/Catalog.module.scss';
import { Context } from '../index';
import { fetchingDevices } from '../http/deviceAPI';
import { fetchingBrands } from '../http/brandAPI';
import Select from '../components/select/Select';
import { observer } from 'mobx-react-lite';
import Loader from '../components/loader/Loader';
import Pagination from '../components/pagination/Pagination';

const Catalog = observer(() => {
    const { store } = useContext(Context);

    const [ devicesLoading, setDevicesLoading ] = useState(true);
    const [ brandsLoading, setBrandsLoading ] = useState(true);
    const changeBrand = value => store.deviceStore.setBrand(value);

    useEffect(() => {
        fetchingDevices(store.deviceStore.type, store.deviceStore.brand, store.deviceStore.page, store.deviceStore.limit).then(data => {
            store.deviceStore.setDevices(data.rows);
            store.deviceStore.setTotalCount(data.count);
        }).finally(() => {
            setDevicesLoading(false);
        });
    }, [ store.deviceStore.page, store.deviceStore.brand, store.deviceStore.type ]);

    useEffect(() => {
        fetchingBrands().then(data => {
            store.brandStore.setBrands(data);
        }).finally(() => {
            setBrandsLoading(false);
        });
    }, []);

    return (
        <SectionTemplate name={'catalog'} styles={styles.catalog}>
            <div className={styles.catalog__wrapper}>
                <Filters/>
                <div className={styles.catalog__content}>
                    <Select
                        options={store.brandStore.brands}
                        name="Сортировка по бренду"
                        prefix="бренды"
                        onChange={changeBrand}
                        allExist={true}
                    />
                    {!devicesLoading
                        ? <Products data={store.deviceStore.devices}/>
                        : <Loader/>
                    }
                    {store.deviceStore.totalCount > 0 && <Pagination/>}
                </div>
            </div>
        </SectionTemplate>
    );
});

export default Catalog;