import { useEffect } from 'react';
import { check } from '../http/userAPI';

export default function useCheckAuth(store) {
    const localToken = localStorage.getItem('token');
    useEffect(() => {
        if (!localToken) return store.setIsAuth(false);
        check()
            .then(data => {
                store.setUser(data);
                store.setIsAuth(true);
            })
            .catch(() => store.setIsAuth(false));
    }, []);
};