import React, { useContext, useEffect } from 'react';
import { Link } from 'react-router-dom';
import SectionTemplate from '../components/SectionTemplate';
import { CATALOG_ROUTE } from '../router/paths';
import styles from './styles/Basket.module.scss';
import { Context } from '../index';
import { fetchingBasketDevices } from '../http/basketAPI';
import { observer } from 'mobx-react-lite';
import BasketList from '../components/basket/BasketList';
import BasketTotal from '../components/basket/BasketTotal';

const Basket = observer(() => {
    const { store } = useContext(Context);
    const basketId = store.userStore.user.id || null;

    useEffect(() => {
        fetchingBasketDevices(basketId)
            .then(data => store.basketStore.setBasket(data.basket_devices))
            .finally(() => store.basketStore.setIsLoading(false))
    }, []);

    return (
        <SectionTemplate name={'basket'} styles={styles.basket}>
            <div className={styles.basket__wrapper}>
                {store.basketStore.basket.length
                    ?
                    <div className={styles.basket__content}>
                        <BasketList data={store.basketStore.basket}/>
                        <BasketTotal/>
                    </div>
                    :
                    <div className={styles.basket__empty}>
                        <h2>В корзине пока ничего ):</h2>
                        <Link to={CATALOG_ROUTE}>Вперёд за покупками!</Link>
                    </div>
                }
            </div>
        </SectionTemplate>
    );
});

export default Basket;