import React from 'react';
import styles from './MenuButton.module.scss'

const MenuButton = ({state, click}) => {

    const clickHandler = e => {
        e.preventDefault();
        click(!state);
    }

    return (
        <button type='button' className={styles.btn} onClick={clickHandler}>
            <div className={state ? [styles.btn__burger, styles._isOpen].join(' ') : styles.btn__burger}>
                <span></span>
                <span></span>
                <span></span>
            </div>
        </button>
    );
};

export default MenuButton;
