import React, { useContext, useEffect, useState } from 'react';
import Counter from '../counter/Counter';
import styles from './BasketItem.module.scss';
import { getFormatPrice } from '../../utils/utils';
import { Link } from 'react-router-dom';
import { CATALOG_ROUTE } from '../../router/paths';
import { changeBasketDevice } from '../../http/basketAPI';
import { Context } from '../../index';
import { observer } from 'mobx-react-lite';

const BasketItem = observer(props => {
    const { store } = useContext(Context);
    const basketId = store.userStore.user.id || null;
    const [ amount, setAmount ] = useState(props.amount);
    const [ price, setPrice ] = useState(props.price * amount);

    useEffect(() => {
        changeBasketDevice(basketId, props.deviceId, amount);
        store.basketStore.setAmount(props.deviceId, amount);
        setPrice(props.price * amount);
    }, [ amount ]);


    return (
        <div className={styles.basket__item}>
            <span className={styles.basket__item_num}>{props.num}.</span>
            <div className={styles.basket__item_main}>
                <Link to={`${CATALOG_ROUTE}/${props.id}`}>{props.name}</Link>
                <p>{props.fullName}</p>
            </div>
            <p className={styles.basket__item_price}>{getFormatPrice(price)}</p>
            <Counter id={props.deviceId} count={amount} setCount={setAmount}/>
        </div>
    );
});

export default BasketItem;
