import { useContext, useState } from 'react';
import { HOME_ROUTE, LOGIN_ROUTE } from '../router/paths';
import { login, registration } from '../http/userAPI';
import { Context } from '../index';
import { useLocation, useNavigate } from 'react-router-dom';
import useValidation from './useValidation';

export default function useFormAuth() {
    const { pathname } = useLocation();
    const navigate = useNavigate();
    const isLogin = pathname === LOGIN_ROUTE;

    const { store } = useContext(Context);
    const [ error, setError ] = useState('');
    const [ state, setState ] = useState({
        email: 'admin@mail.ru',
        password: 'root',
        errors: { emailErr: '', passwordErr: '' },
        emailIsValid: true,
        passwordIsValid: true,
    });
    const {emailValidation, passwordValidation} = useValidation(state, setState);

    const changeHandler = e => {
        setError('');
        switch (e.target.name) {
            case 'email':
                emailValidation(e.target.value);
                break;
            case 'password':
                passwordValidation(e.target.value);
                break;
        }
    };

    const authAndRegistrationHandler = async e => {
        e.preventDefault();
        if (!state.emailIsValid || !state.passwordIsValid) return;

        const authSuccess = (data) => {
            store.userStore.setUser(data);
            store.userStore.setIsAuth(true);
            navigate(HOME_ROUTE);
        };
        const authError = (res) => {
            setError(res.response.data.message);
            store.userStore.setIsAuth(false);
        };

        if (isLogin) {
            await login(state.email, state.password)
                .then(data => authSuccess(data))
                .catch(res => authError(res));
        } else {
            await registration(state.email, state.password)
                .then(data => authSuccess(data))
                .catch(res => authError(res));
        }
    };

    return { isLogin, error, state, changeHandler, authAndRegistrationHandler };
}