import React from 'react';
import { regexpCheck } from '../utils/utils';
import { REGEXPS } from '../types/types';

const useValidation = (state, setState) => {
    const emailValidation = value => {
        !regexpCheck(REGEXPS.EMAIL, value) && value !== ''
            ? setState(
                {
                    ...state,
                    email: value,
                    errors: { ...state.errors, emailErr: 'Неккоректный email' },
                    emailIsValid: false
                }
            )
            : setState(
                {
                    ...state,
                    email: value,
                    errors: { ...state.errors, emailErr: '' },
                    emailIsValid: value === '' ? undefined : true
                }
            );
    };
    const passwordValidation = value => {
        value.length < 3 && value !== ''
            ? setState(
                {
                    ...state,
                    password: value,
                    errors: { ...state.errors, passwordErr: 'Неккоректный пароль' },
                    passwordIsValid: false
                }
            )
            : setState(
                {
                    ...state,
                    password: value,
                    errors: { ...state.errors, passwordErr: '' },
                    passwordIsValid: value === '' ? undefined : true
                }
            );
    };

    return {emailValidation, passwordValidation};
};

export default useValidation;
