import { $authHost, $host } from './index';

export async function createRating(userId, deviceId, rate) {
    const response = await $authHost.post('/api/rating', {userId, deviceId, rate});
    return response.data;
}

export async function fetchingAllRatingsById(deviceId) {
    const response = await $host.get('/api/rating/' + deviceId);
    return response.data;
}

export async function fetchingUserRatingById(userId, deviceId) {
    const response = await $host.get(`/api/rating/${userId}/${deviceId}`);
    return response.data;
}