import { $authHost, $host } from './index';

export async function createDevice(device) {
    const response = await $authHost.post('/api/device', device);
    return response.data;
}

export async function fetchingDevices(typeId, brandId, page= 1, limit = 5) {
    const response = await $host.get('/api/device', {
        params: {
            typeId, brandId, page, limit
        }
    });
    return response.data;
}

export async function fetchingOneDevice(id) {
    const response = await $host.get('/api/device/' + id);
    return response.data;
}

export async function updateDevice(id, updateData) {
    const response = await $host.patch('/api/device/' + id, updateData);
    return response.data;
}