import React, { useContext } from 'react';
import styles from './Alert.module.scss';
import './Alert.theme.scss';
import { Context } from '../../index';
import { observer } from 'mobx-react-lite';

const Alert = observer(() => {
    const { store } = useContext(Context);
    const rootClasses = [styles.alert, `alert__${store.alertStore.state.type}`].join(' ');
    return (
        <>
            {store.alertStore.state.visible &&
                <div className={store.alertStore.state.type.length ? rootClasses : styles.alert}>
                    <p>{store.alertStore.state.text}</p>
                </div>
            }
        </>
    );
});

export default Alert;
